.btn{
    width: 100%;
    height: 50px;
    background-color: #ffffff;
}
.btn1{
    padding-top: 2%; 
    background-color: #ffffff;
    color: darkgray;
    border: none;
    padding-left: 4%;
}
.btn1 i{
    color: darkgray;
}
.btn2{
    font-weight: bolder;
    color: #ffffff;
    border: none;
    background-color: #ffad01;
    margin-left: 4%;
    width: 30%;
    border-radius: 25px 0px 0px 25px;
}
.btn3{
    width: 30%;
    border: none;
    color: #ffffff;
    font-weight: bolder;
    background-color: #ff6805;
    border-radius: 0px 25px 25px 0px;
}